#interests {
    padding: 8rem 0;
}

#interests > h3 {
    text-align: center;
    text-transform: uppercase;
    font-size: 2rem; font-weight: 700;
    margin-bottom: 8rem;
}

#interests > h3 .dot {
    display: inline-block;
    position: relative;
    top: 5px;
    border-radius: 50%;
    background: var(--goldish);
    height: 10px; width: 10px;
}

#interests .row {
    display: flex; justify-content: center; align-items: center;
}
#interests img {
    width: 100%; height: 100%; border-radius: 0 !important;
}
#interests .row .col-md-6 p {
    padding: 1% 5%;
    font-weight: 500; font-size: 1.125rem;
    margin-bottom: 20px; display: block;
}
#interests p::first-letter {
   margin-left: 5%;
}

#interests .row .col-md-6 h4 {
    padding: 5% 5% 1%;
    font-weight: 700; color: rgb(33, 37, 41);
    text-transform: uppercase;
    letter-spacing: 2px; margin-bottom: 20px;
}

/*
-------------------------------------------
MEDIA QUERIES
-------------------------------------------
*/

/* EXTRA SMALL*/
@media (max-width: 575.98px) {
 #interests .container .row .col-sm-12 {
     width: 90%;
 }
 
}
/* SMALL */
@media (max-width: 767.98px) {
    #interests {
        padding: 6rem 0 3rem;
    }
    #interests > h3 {
        margin-bottom: 4rem;
    }
    #interests .row:nth-child(2) .col-md-6:nth-child(1) {
        order: 2;
    }

    #interests .row:nth-child(2) .col-md-6:nth-child(2) {
        order: 1;
    }
    #interests .container .row .col-sm-12 p {
        padding: 0;
    }
}

/* MEDIUM */
@media (min-width: 768px) and (max-width: 991.98px) {
    #interests .row .col-md-6 h4 {
        padding: 5% 5% 1%; font-size: 1.5rem; margin-bottom: 10px;
        color: rgb(33, 37, 41);
        text-transform: uppercase;
        letter-spacing: 2px; line-height: 20px;
    }
    #interests .row .col-md-6 p {
        padding: 1% 5%; margin-bottom: 10px;
        font-size: .95rem;
    }
}

